.about-page {
  width: 100%;
  position: fixed;
  background-color: var(--warna-cream);
  color: var(--warna-biru);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.about-page.navbar-aktif {
  height: 100vh;
  overflow: hidden;
}
.about-page .cursor {
  display: none;
  pointer-events: none;
}
@media screen and (min-width: 64rem) {
  .about-page .cursor {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    z-index: 100;
    border: var(--warna-biru) solid 1px;
  }
}
.about-page .about-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding-bottom: 5rem;
  padding-top: 5rem;
}
.about-page .about-container .foto-container {
  margin-top: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 21rem;
  max-height: 21rem;
  width: 73vw;
  height: 73vw;
  border-radius: 50%;
  border: red 1px solid;
}
.about-page .about-container .foto-container img {
  max-width: 20rem;
  width: 70vw;
}
.about-page .about-container .about-isi {
  margin-top: 2rem;
  padding: 2rem 2rem;
  border: 1px solid var(--warna-biru);
}
.about-page .about-container .about-isi h1 {
  font-size: 2rem;
}
@media screen and (min-width: 64rem) {
  .about-page .about-container .about-isi h1 {
    font-size: 3rem;
  }
}
.about-page .about-container .about-isi h2 {
  font-size: 1.5rem;
}
@media screen and (min-width: 64rem) {
  .about-page .about-container .about-isi h2 {
    font-size: 2.5rem;
  }
}
.about-page .about-container .about-isi h3 {
  line-height: 1.5;
}
.about-page .about-container .about-isi .saya-wrapper {
  margin-top: 1rem;
}
.about-page .about-container .about-isi .profile-wrapper {
  margin-top: 1rem;
}
.about-page .about-container .about-isi .biodata-wrapper {
  margin-top: 1rem;
}
.about-page .about-container .about-isi .biodata-wrapper .pendidikan {
  border-bottom: 1px solid var(--warna-biru);
}